<template>
  <section id="menu">
    <h1>Menu</h1>
    <main>
      <div class="menuNav">
        <h3 v-for="m in menu" :key="m.id" @click="clickedMenuId = m.id">
          {{ m.name }} <span class="active" v-if="clickedMenuId == m.id"></span>
        </h3>
      </div>
      <div class="offers">
        <div
          v-for="actualMenu in clickedMenu"
          v-bind:key="actualMenu.name"
          class="offer"
        >
          <h4>{{ actualMenu.name }}</h4>
          <p class="description">
            {{ actualMenu.description }}
          </p>
          <p class="price">{{ actualMenu.price }} Kč</p>
        </div>
      </div>
    </main>
  </section>
</template>

<script>
export default {
  name: "Menu",
  data() {
    return {
      menu: [
        {
          id: 0, //Main food
          name: "Hlavní jídla",
          food: [
            {
              name: "Vegeburger",
              description:
                "Burger na bázi sójových bobů s česnekovým dipem a zeleninou. V celozrnné či sezonní housce.",
              price: 223,
            },
            {
              name: "Zeleninové wrapy",
              description:
                "Různá sezónní zelenina zabalená v zelí. Podáváno s dipem.",
              price: 170,
            },
            {
              name: "Celozrnné plněné wrapy",
              description:
                "Celozrnné wrapy plněné zeleninou a „vegemasem“ ze seitanu.",
              price: 210,
            },
          ],
        },
        {
          id: 1, //Deserts
          name: "Zákusky",
          food: [
            {
              name: "Čajový puding",
              description:
                "Puding ochucený různými čaji. Pro ztužení se používá agar agar.",
              price: 70,
            },
            {
              name: "Kokosový dort",
              description: "Borůvkovo kokosový dort typu „cheesecake“.",
              price: 55,
            },
            {
              name: "Kaše",
              description: "Kaše z různého sezonního ovoce.",
              price: 55,
            },
          ],
        },
        {
          id: 2, //Drinks
          name: "Nápoje",
          food: [
            {
              name: "Sezonní limonáda",
              description: "",
              price: 40,
            },
            {
              name: "Káva",
              description: "",
              price: 40,
            },
            {
              name: "Káva s přírodním mlékem",
              description: "",
              price: 55,
            },
            {
              name: "Voda",
              description: "",
              price: 12,
            },
          ],
        },
      ],
      clickedMenuId: 0,
    };
  },
  computed: {
    clickedMenu() {
      return this.menu.find((m) => m.id === this.clickedMenuId).food;
    },
  },
};
</script>

<style lang="scss" scoped>
#menu {
  @include sectionMarginTop();
  h1 {
    width: 100%;
    text-align: center;
    color: $green;
    font-family: "Dancing Script";
  }
  main {
    max-width: 1280px;
    margin-top: 1rem;
    display: flex;
    flex-wrap: wrap;
    .menuNav {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-items: center;
      padding: 2rem;
      border-bottom: 3px solid $grey;
      h3 {
        padding: 1rem;
        font-weight: 600;
        font-size: 1.5rem;
        display: inline-block;
        position: relative;
        color: $green;
        @include mobile() {
          font-size: 1rem;
          @include flexCenter();
          text-align: center;
        }
        &:hover {
          cursor: pointer;
        }
        &:after {
          content: "";
          position: absolute;
          width: 100%;
          transform: scaleX(0);
          height: 3px;
          bottom: 0;
          left: 0;
          background-color: $green;
          transform-origin: bottom right;
          transition: transform 0.25s ease-out;
        }
        &:hover:after {
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }
    }
    .active {
      position: absolute;
      width: 100%;
      height: 3px;
      bottom: 0;
      left: 0;
      background-color: $green;
    }
    .offers {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-items: center;
      text-align: center;
      gap: 5rem;
      padding: 2rem;
      width: 100%;
      @include mobile() {
        grid-template-columns: 1fr;
        max-height: 200px;
        overflow-y: auto;
        border-bottom: 3px solid $grey;
        gap: 1rem;
      }
      .offer {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        flex-direction: column;
        h4 {
          font-size: 1.5rem;
          font-weight: bold;
        }
        p {
          margin: 1rem 0;
        }
        .price {
          font-weight: 500;
        }
      }
    }
  }
}
</style>
