<template>
  <div class="nutritionCalculation">
    <FoodSelection @selected="add" />
    <div class="spacer"></div>
    <FoodSelected :food="selectedFood" @remove="remove" />
  </div>
</template>

<script>
import FoodSelection from "@/components/nutriCalc/FoodSelection.vue";
import FoodSelected from "@/components/nutriCalc/FoodSelected.vue";

export default {
  name: "NutritionCalculationView",
  components: {
    FoodSelection,
    FoodSelected,
  },
  data() {
    return {
      selectedFood: [],
    };
  },
  methods: {
    add(food) {
      console.log(food);
      if (!this.selectedFood.some((f) => f.id === food.id)) {
        this.selectedFood.push({ amount: 1, ...food });
      } else {
        this.selectedFood.find((f) => f.id === food.id).amount++;
      }
    },
    remove(id) {
      this.selectedFood = this.selectedFood.filter((f) => f.id !== id);
    },
  },
};
</script>

