import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
import NutritionCalculationView from "../views/NutritionCalculationView.vue";
import PartnersView from "../views/PartnersView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/nutritionCalculation",
    name: "nutritionCalculation",
    component: NutritionCalculationView,
  },
  {
    path: "/partners",
    name: "partnersView",
    component: PartnersView,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
