<template>
  <div id="app">
    <nav :class="{ navActive: navIsActive }">
      <router-link class="logo" to="/"
        ><img src="@/assets/img/mainLogo.svg" alt=""
      /></router-link>
      <ul :class="{ openNav: openNav }">
        <li>
          <router-link to="/" v-scroll-to="'#landingScrollTo'"
            >Domů</router-link
          >
        </li>
        <li>
          <router-link to="/nutritionCalculation"
            >Nutriční kalkulace</router-link
          >
        </li>
        <li>
          <router-link to="/" v-scroll-to="'#menuScrollTo'">Menu</router-link>
        </li>
        <li>
          <router-link to="/" v-scroll-to="'#aboutUsScrollTo'"
            >O nás</router-link
          >
        </li>
        <li>
          <router-link to="/" v-scroll-to="'#gallery'">Galerie</router-link>
        </li>
        <li>
          <router-link class="btnSq" to="/" v-scroll-to="'#contact'"
            >Kontakt</router-link
          >
        </li>
      </ul>
      <div class="hamburger">
        <div class="icon" @click="OpenNav">
          <font-awesome-icon icon="fa-solid fa-bars" v-if="openNav" />
          <font-awesome-icon icon="fa-solid fa-xmark" v-else />
        </div>
      </div>
    </nav>
    <router-view />
    <Footer />
  </div>
</template>
<script>
import Footer from "@/components/Footer.vue";

export default {
  components: {
    Footer,
  },
  data() {
    return {
      navIsActive: false,
      openNav: true,
    };
  },
  methods: {
    OnScroll() {
      if (window.scrollY > 20) {
        this.navIsActive = true;
      } else {
        this.navIsActive = false;
      }
    },
    OpenNav() {
      this.openNav = !this.openNav;
    },
  },
  created() {
    window.addEventListener("scroll", this.OnScroll);
  },
};
</script>

